import React, { Component } from 'react'
import Card, { CardActions, CardContent, CardMedia } from 'material-ui/Card';
import Typography from 'material-ui/Typography'
import Button from 'material-ui/Button'
import { CircularProgress } from 'material-ui/Progress'
import Paper from 'material-ui/Paper'
import { InputLabel } from 'material-ui/Input'
import { MenuItem } from 'material-ui/Menu'
import { FormControl } from 'material-ui/Form'
import Select from 'material-ui/Select'
import { FormControlLabel } from 'material-ui/Form'
import Switch from 'material-ui/Switch'
import green from 'material-ui/colors/green'
import { withStyles } from 'material-ui/styles'
import Refresh from 'material-ui-icons/Refresh'
import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog'

const stylesBox = {
  checked: {
    color: green[500],
    '& + $bar': {
      backgroundColor: green[500],
    },
  },
  bar: {},
  rightIcon: {
    marginLeft: 8,
  }
}

const styles = {
  img: {
    width: 150,
    height: 100,
    backgroundSize: 'inherit',
    margin: '0 auto'
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    alignItems: 'center',
    paddingTop: 96
  },
  card: {
    flexBasis: 300,
    marginBottom: 16
  },
  loading: {
    position: 'absolute',
    top: '50%',
    transform: 'translateY(-50%)'
  },
  circle: {
    margin: '8px auto',
    display: 'block'
  },
  param: {
    display: 'flex',
    justifyContent: 'space-between'
  },
  header: {
    minHeight: 80,
    width: '100%',
    position: 'fixed',
    top: 0,
    left: 0,
    padding: '0 24px',
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    alignItems: 'center',
    boxSizing: 'border-box',
    zIndex: 100
  },
  input: {
    width: 240
  },
  checkbox: {
    color: green[500],
    '& span': {
      backgroundColor: green[500]
    }
  } 
}

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      items: [],
      filteredItems: [],
      loading: true,
      levelStart: 1,
      levelEnd: 21,
      subtype: 0,
      weapon: true,
      armor: true,
      open: false
    }
    this.handleRefresh = this.handleRefresh.bind(this)
  }

  componentDidMount(){
    const that = this
    let items = []
    const limit = 1300

    for(let i = 0; i <= limit; i++){
      fetch(`http://api.unit-online.ru/item?id=${i}`)
        .then((data) => {
          data.json().then((res) => {
            if(res.status === 'ok') {
              items.push(res.item)
              console.log(items.length)
            }
            if(i === limit) that.setState({items, loading: false}, () => {
              console.log('alo')
              console.log('items', items.length)
              that.goFilter()
            })
          })
        })
    }
  }

  goFilter(){
    const {levelStart, levelEnd, subtype, weapon, armor, items} = this.state
    
    if(levelStart > levelEnd) {
      this.setState({open: true})
      return false
    }
    
    let filteredItems = items.filter((item) => {
      const {params} = item
      let level
      for (let key in params) {
        if(params[key].name === "Уровень") level = params[key].value
      }
      if(levelStart <= level && level <= levelEnd) return true
      return false
    })

    filteredItems = filteredItems.filter((item) => {
      if(item.type === 'Оружие' && weapon){
        if(!subtype) return true
        else{
          if(subtype === item.subtype) return true
          return false
        }
      }
      if((item.type === 'Шлем' || item.type === 'Броня' || item.type === 'Ботинки' || item.type === 'Штаны') && armor) return true
      return false
    })

    this.setState({filteredItems})
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value }, () => this.goFilter())
  }

  handleChangeCheckbox = name => event => {
    this.setState({ [name]: event.target.checked }, () => this.goFilter())
  }

  handleClose = () => {
    this.setState({ open: false })
  }

  handleRefresh(){
    this.setState({
      levelStart: 1,
      levelEnd: 21,
      subtype: 0,
      weapon: true,
      armor: true
    }, () => this.goFilter())
  }

  render() {
    const {classes} = this.props
    return (
      <div style={styles.container}>
        <Dialog
          open={this.state.open}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">КЕК</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {`То есть, ты всерьёз хочешь найти шмотки уровень которых выше ${this.state.levelStart}, но при этом ниже чем ${this.state.levelEnd}?`}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              Нет, я дебил
            </Button>
            <Button onClick={this.handleClose} color="primary" autoFocus>
              Да, я дебил
            </Button>
          </DialogActions>
        </Dialog>
        {this.state.loading ? (
          <div style={styles.loading}>
            <Typography variant="headline" component="h2">
              Загрузка
            </Typography>
            <CircularProgress size={50} style={styles.circle}/>
          </div>
        ) : (
          <Paper elevation={4} style={styles.header}>
            <FormControl style={styles.input}>
              <InputLabel htmlFor="levelStart">Уровень от</InputLabel>
              <Select
                value={this.state.levelStart}
                onChange={this.handleChange}
                inputProps={{
                  name: 'levelStart',
                  id: 'levelStart',
                }}
              >
                <MenuItem value={1}>1</MenuItem>
                <MenuItem value={2}>2</MenuItem>
                <MenuItem value={3}>3</MenuItem>
                <MenuItem value={4}>4</MenuItem>
                <MenuItem value={5}>5</MenuItem>
                <MenuItem value={6}>6</MenuItem>
                <MenuItem value={7}>7</MenuItem>
                <MenuItem value={8}>8</MenuItem>
                <MenuItem value={9}>9</MenuItem>
                <MenuItem value={10}>10</MenuItem>
                <MenuItem value={11}>11</MenuItem>
                <MenuItem value={12}>12</MenuItem>
                <MenuItem value={13}>13</MenuItem>
                <MenuItem value={14}>14</MenuItem>
                <MenuItem value={15}>15</MenuItem>
                <MenuItem value={16}>16</MenuItem>
                <MenuItem value={17}>17</MenuItem>
                <MenuItem value={18}>18</MenuItem>
                <MenuItem value={19}>19</MenuItem>
              </Select>
            </FormControl>
            <FormControl style={styles.input}>
              <InputLabel htmlFor="levelEnd">Уровень до</InputLabel>
              <Select
                value={this.state.levelEnd}
                onChange={this.handleChange}
                inputProps={{
                  name: 'levelEnd',
                  id: 'levelEnd',
                }}
              >
                <MenuItem value={1}>1</MenuItem>
                <MenuItem value={2}>2</MenuItem>
                <MenuItem value={3}>3</MenuItem>
                <MenuItem value={4}>4</MenuItem>
                <MenuItem value={5}>5</MenuItem>
                <MenuItem value={6}>6</MenuItem>
                <MenuItem value={7}>7</MenuItem>
                <MenuItem value={8}>8</MenuItem>
                <MenuItem value={9}>9</MenuItem>
                <MenuItem value={10}>10</MenuItem>
                <MenuItem value={11}>11</MenuItem>
                <MenuItem value={12}>12</MenuItem>
                <MenuItem value={13}>13</MenuItem>
                <MenuItem value={14}>14</MenuItem>
                <MenuItem value={15}>15</MenuItem>
                <MenuItem value={16}>16</MenuItem>
                <MenuItem value={17}>17</MenuItem>
                <MenuItem value={18}>18</MenuItem>
                <MenuItem value={19}>19</MenuItem>
                <MenuItem value={20}>20</MenuItem>
                <MenuItem value={21}>21</MenuItem>
              </Select>
            </FormControl>
            <FormControl style={styles.input}>
              <InputLabel htmlFor="subtype">Тип</InputLabel>
              <Select
                value={this.state.subtype}
                onChange={this.handleChange}
                inputProps={{
                  name: 'subtype',
                  id: 'subtype',
                }}
              >
                <MenuItem value={0}>Все</MenuItem>
                <MenuItem value={"Ближний бой"}>Ближний бой</MenuItem>
                <MenuItem value={"Лёгкое оружие"}>Лёгкое</MenuItem>
                <MenuItem value={"Тяжёлое оружие"}>Тяжёлое</MenuItem>
                <MenuItem value={"Энергетическое оружие"}>Энергетическое</MenuItem>
              </Select>
            </FormControl>
            <FormControlLabel
              className='checkbox'
              control={
                <Switch
                  checked={this.state.weapon}
                  onChange={this.handleChangeCheckbox('weapon')}
                  value="weapon"
                  classes={{
                    checked: classes.checked,
                    bar: classes.bar,
                  }}
                />
              }
              label="Оружие"
            />
            <FormControlLabel
              className='checkbox'
              control={
                <Switch
                  checked={this.state.armor}
                  onChange={this.handleChangeCheckbox('armor')}
                  value="armor"
                  classes={{
                    checked: classes.checked,
                    bar: classes.bar,
                  }}
                />
              }
              label="Броня"
            />
            <Button className={classes.button} variant="raised" color="primary" onClick={this.handleRefresh}>
              Сбросить фильтр
              <Refresh className={classes.rightIcon} />
            </Button>
          </Paper>
        )}
        {this.state.filteredItems.map((item, index) => {
          return (
            <Card key={index} style={styles.card}>
              <CardMedia image={item.image} style={styles.img}/>
              <CardContent>
                <Typography variant="headline" component="h2">
                  {item.name}
                </Typography>
                {item.params.map((param, i) => {
                  if(param.name !== "Клетки (промах %)"){
                    return (
                      <div key={i} style={styles.param}>
                        <Typography>
                          {param.name}
                        </Typography>
                        <Typography>
                          {param.value}
                        </Typography>
                      </div>
                    )
                  }
                  else return ''
                })}
              </CardContent>
              <CardActions>
                <Button size="small" color="primary">
                  потыкать кнопку
                </Button>
                <Button size="small" color="primary">
                  и ещё
                </Button>
              </CardActions>
            </Card>
          )
        })}
      </div>
    )
  }
}

export default withStyles(stylesBox)(App)


